	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				facData:{},
				info:{},
				pdfShow:false,
				videoShow:false,
				isOne:false,
				isTwo:false,
				pdfFlag:false,
				videoFlag:false,
				btnFlag:false,
                pageFlag:'',
				hotelid:''
			};
		},
        created:function () {
			this.info =  this.$route.query.data
			this.pageFlag = localStorage.menuFlag
	        this.facData = this.$route.query.data;
			if(this.facData.pdf && this.info.video){
				this.btnFlag = true;
				this.pdfFlag = true;
				this.videoFlag = true;
				this.isTwo = true;
			}else if(this.facData.pdf){
				this.btnFlag = true;
				this.pdfFlag = true;
				this.isOne = true;
			}else if(this.facData.video){
				this.btnFlag = true;
				this.videoFlag = true;
				this.isOne = true;
			}
		},
		methods: {
			toPDF:function () {
				if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
					openFile(this.info.pdf)
				}else{
					openPdf(this.info.pdf)
				}

			},
			toVideo: function () {
				if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
					openFile(this.info.video)
				}else{
					openVideo(this.info.video)
				}
			},
		},
	};



