//	import pdf from 'vue-pdf'

    export default {
        data() {
            return {
                info:{},
	            pdfShow:false,
	            videoShow:false,
	            isOne:false,
	            isTwo:false,
	            pdfFlag:false,
	            videoFlag:false,
	            btnFlag:false

            };
        },
        created:function () {
			this.info = this.$route.query.info;
	        if(this.info.pdf && this.info.video){
	        	this.btnFlag = true;
	        	this.pdfFlag = true;
	        	this.videoFlag = true;
	        	this.isTwo = true;
	        }else if(this.info.pdf){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.isOne = true;
	        }else if(this.info.video){
		        this.btnFlag = true;
		        this.videoFlag = true;
		        this.isOne = true;
	        }


        },
        methods: {
            toPDF:function () {
	            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
		            openFile(this.info.pdf)
	            }else{
		            openPdf(this.info.pdf)
	            }

            },
            toVideo: function () {
	            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
		            openFile(this.info.video)
	            }else{
		            openVideo(this.info.video)
	            }
            },
			closeVideo:function () {
//                this.sources[0].src = '';
				this.videoFlag = false;
                this.videoUrl = '';

            },
	        play() {
		        console.log('play callback')
	        }

        },
        mounted() {

        },
        components:{



        }
    };



