    import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
    export default {
        data() {
            return {
            };
        },
        created:function () {

        },
        methods: {
        	//
			goBack:function () {
				let flag = localStorage.menuFlag;
				switch (flag){
					//
					case 'home':
						this.$router.replace('/home')
						break;
					//
					case 'unlogin':
						this.$router.replace('/unlogin')
						break;
					//
					case 'guide':
						this.$router.replace('/guide')
						break;
					//
					case 's_home':
						this.$router.replace('/s_home')
						break;
				}

			}

        },
        mounted() {

        },
        components:{

        },
    };



