
    import PopupPicker from 'vux/src/components/popup-picker/index.vue'

    import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
    import foot from '../foot.vue'
    import menu from '../common/menu.vue'
    export default {
        data() {
            return {
                isItemShow:false,
				dataList:[],
				tagList:[],
				nextPage:1,
				tagId:-1,
                pullupConfig: {
                    content: '',
                    downContent: '',
                    upContent: '',
                    loadingContent: '...'
                }

            };
        },
        created:function () {
            //
			let params = {'hotelid':localStorage.HOTELID};
            this.$store.dispatch('getActivityTagList', params).then((res) => {
//                alert(1);
                this.tagList = [{name:'',value:-1}];
                let list =res.data.data.list;
                for(var key in list){
                    this.tagList.push({name:list[key].title,value:list[key].id});
                }
            }).catch((res) => {
            });
            params = {'hotelid':localStorage.HOTELID,'lang':localStorage.LANGUAGE};
            this.$store.dispatch('getActivityList', params).then((res) => {
                this.dataList = res.data.data.list;
                this.nextPage = res.data.data.nextPage;
            }).catch((res) => {
            });

        },
        methods: {
            toTag:function (tagid) {
				this.tagId = tagid;
                let params = {'hotelid':localStorage.HOTELID,'lang':localStorage.LANGUAGE};
                if (tagid != -1){
                    params.tagid = tagid;
				}
                this.$store.dispatch('getActivityList', params).then((res) => {
                    this.dataList = res.data.data.list;
                    this.nextPage = res.data.data.nextPage;
                }).catch((res) => {
                });
            },

            //
            infinite: function (done) {
                if (this.nextPage == -1) return;
                let params = {'hotelid':localStorage.HOTELID,'lang':localStorage.LANGUAGE,'page':this.nextPage};
                this.$store.dispatch('getActivityList', params).then((res) => {
//                    this.dataList = res.data.data.list;
                    this.dataList.push.apply(this.dataList,res.data.data.list);
                this.nextPage = res.data.data.nextPage;
            }).catch((res) => {
                });
            },
			more :function (id) {
                let data = {};
                for(var key in this.dataList){
                    if (this.dataList[key].id == id){
                        data = this.dataList[key];
                        break;
                    }
                }
                this.$router.replace({path:'/propertyDetail',query:{info:data}});
            }


        },
        components: {
            'v-foot':foot,
            'v-menu':menu,
            PopupPicker,
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        }

    };



