	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				newsList:{},
                newsDetail:{}
			};
		},
        created:function () {
			let newsid = this.$route.query.newsid;
			alert('news = '+newsid)
	        //
	        let params = {
		        hotelid: localStorage.HOTELID,
		        groupid: 1,
		        platform:localStorage.platform,
		        identity:localStorage.identity
	        }
	        this.$store.dispatch('getNews', params).then((res) => {
		        if(res.code == 0){
                    this.newsList = res.data.data;

                    for(item in this.newsList){
                    	if(this.newsList[item].msgId == newsid){
		                    this.newsDetail = this.newsList[item];
                        }
                    }

		        }else{
			        this.$dialog.toast({mes: res.msg, timeout: 1000});
		        }
	        })


        },
		methods: {

		},
		computed: {
		},
	};



