    export default {
        data() {
            return {
                data:{},
				srcUrl:'',
	            pdfShow:false,
	            videoShow:false,
	            isOne:false,
	            isTwo:false,
	            pdfFlag:false,
	            videoFlag:false,
	            btnFlag:false
            };
        },
        created:function () {
			this.data = this.$route.query.data;
	        if(this.data.pdf && this.data.video){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.videoFlag = true;
		        this.isTwo = true;
	        }else if(this.data.pdf){
		        this.btnFlag = true;
		        this.pdfFlag = true;
		        this.isOne = true;
	        }else if(this.data.video){
		        this.btnFlag = true;
		        this.videoFlag = true;
		        this.isOne = true;
	        }
        },
        methods: {
	        toPDF:function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.data.pdf)
		        }else{
			        openPdf(this.data.pdf)
		        }

	        },
	        toVideo: function () {
		        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			        openFile(this.data.video)
		        }else{
			        openVideo(this.data.video)
		        }
	        },
	        closeVideo:function () {
//                this.sources[0].src = '';
		        this.videoFlag = false;
		        this.videoUrl = '';
	        },
	        play() {
		        console.log('play callback')
	        }
        },
        mounted() {

        },
        components:{

        }
    };



