    import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                info:{},
				num:'',
	            spinner1:0

            };
        },
        created:function () {

        },
	    activated:function () {
		    this.info = this.$route.query.info;
		    console.log('info',this.info);
	    },
        methods: {

            apply: function () {
               if(!this.num){
                    this.$dialog.toast({mes: this.language.msg.empty_num_buy, timeout: 1000});
                    return;
				}
                let params = {'hotelid':localStorage.HOTELID,'shoppingid':this.info.id,'token':localStorage.TOKEN,'count':this.spinner1};
                this.$store.dispatch('getShoppingOrder', params).then((res) => {
                }).catch((res) => {
                });
            }

        },
        components:{

        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },
    };



