
    import { mapState } from 'vuex'
    import { mapGetters } from 'vuex'
    import foot from '../foot.vue'
    import menu from '../common/menu.vue'
    import guestCenter from '../common/menu/guestCenter.vue'
    import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

    import { getDistance } from '../../config/mUtils'
	export default {
		data() {
			return {
				menuFlag:false,
                centerFlag:false,
                isItemShow:false,
                tagList:[],
                typeList:[],
                poiList:[],
                tagId:-1,
                typeId:1,
                nextPage:-1,
			};
		},
        created:function () {
            //
            localStorage.footFlag = 3;
            let params = {'hotelid':localStorage.HOTELID,'lang':localStorage.LANGUAGE};
            //
            this.$store.dispatch('getPoiTypeList', params).then((res) => {
                this.typeList = res.data.data.list
                //
                if (this.typeList.length>0){
                    let typeId = this.typeList[0].id;
                    this.typeId = typeId;
                    let params = {'hotelid':1,'typeid':typeId,'lang':'zh'};
                    this.$store.dispatch('getPoiList', params).then((res) => {
                        var list =  res.data.data.list;
                        for (var key in list){
                            let distance = parseFloat(getDistance(localStorage.lng, localStorage.lat, list[key].lat, list[key].lng)).toFixed(1);
                            list[key].distance = distance;
                            this.poiList.push(list[key]);
                        }
//                        this.poiList = res.data.data.list;
                    }).catch((res) => {
                    });
                }
                $(document).ready(function () {
	                var swiper = new Swiper('.raiders .swiper-container', {
		                pagination: '.swiper-pagination',
		                slidesPerView: 3,
		                paginationClickable: true,
		                spaceBetween: 0
	                });
	                $('.ra').on('click','.swiper-slide',function () {
		                $(this).addClass('active').siblings().removeClass('active')
	                })

                })

            }).catch((res) => {
            });
            //
            params = {'hotelid':localStorage.HOTELID,'lang':localStorage.LANGUAGE};
            this.$store.dispatch('getPoiTagList', params).then((res) => {
                let list = res.data.data.list;
                //
                this.tagList = [{name:'',value:-1}];
                for(var key in list){
                    this.tagList.push({name:list[key].title,value:list[key].id});
                }

	        var swiper = new Swiper('.raiders .swiper-container', {
		        pagination: '.swiper-pagination',
		        slidesPerView: 3,
		        paginationClickable: true,
		        spaceBetween: 0
	        });

            });



        },
		methods: {
            more:function (id) {
//                window.location.href = url;
                let data = {};
                for(var key in this.poiList){
                    if (this.poiList[key].id == id){
                        data = this.poiList[key];
                        break;
                    }
                }
                this.$router.replace({path:'/raidersDetail',query:{data:data}});
            },
            toType:function (typeid) {
                var lat = 1;
                var lng = 1;
                this.tagId = -1;
                this.typeId = typeid;
                let params = {'hotelid':1,'typeid':typeid,'lang':'zh'};
                this.$store.dispatch('getPoiList', params).then((res) => {
                    var list =  res.data.data.list;
                    this.poiList = [];
                    for (var key in list){
                        let distance = parseFloat(getDistance(localStorage.lng, localStorage.lat, list[key].lat, list[key].lng)).toFixed(2);
                        list[key].distance = distance;
                        this.poiList.push(list[key]);
                    }
                    this.nextPage = res.data.data.nextPage;
                }).catch((res) => {
                });
            },
            onChangeTag:function (tagid) {
                var lat = 1;
                var lng = 1;
                let params = {'hotelid':1,'typeid':this.typeId,'tagid':tagid,'lang':'zh'};
                this.$store.dispatch('getPoiList', params).then((res) => {
                    var list =  res.data.data.list;
                    this.poiList = [];
                    for (var key in list){
                        let distance = parseFloat(getDistance(localStorage.lng, localStorage.lat, list[key].lat, list[key].lng)).toFixed(2);
                        list[key].distance = distance;
                        this.poiList.push(list[key]);
                    }
                    this.nextPage = res.data.data.nextPage;
                }).catch((res) => {
                });
            },
            //
            infinite: function (done) {
                if(this.nextPage == -1) return;
                let params = {'hotelid':1,'typeid':this.typeId,'tagid':this.tagid,'lang':'zh'};
                this.$store.dispatch('getPoiList', params).then((res) => {
                    this.poiList.push.apply(this.poiList,res.data.data.list);
                    this.nextPage = res.data.data.nextPage;
                }).catch((res) => {
                });

                //test code
/*
                setTimeout(() => {
                    var start = this.poiList.length + 1;
                    for (let i = start; i <= start + 2; i++) {
                        let obj = {
                            name: '',
                            address: '',
                            updatetime: new Date().getTime(),
                            pic: 'https://www.baidu.com/img/bd_logo1.png'
                        }
                        this.poiList.push(obj);
                    }
                    done();
                }, 1500)
*/
                            }

		},
		mounted() {

		},
        components:{
			'v-foot':foot,
            'v-menu':menu,
            'v-guestCenter':guestCenter,
            PopupPicker,

        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },
	};



