	import { mapGetters } from 'vuex'
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				languageShow:false,
                menuFlag:''
             };
		},
        created:function () {
			//
	        if( localStorage.TOKEN ){
		        this.languageShow = true;
	        }
	        //
	        this.menuFlag = localStorage.menuFlag
        },
		methods: {
			tabLanguage:function (key) {
				this.$store.dispatch('changeLanguage');
		    },
            help:function () {
                this.$router.replace({path:'/help',query:{menuFlag : this.menuFlag}})
            },
            emSign:function () {
                this.$router.replace('loginForEmployee');
            },
            aboutUs: function () {
                this.$router.replace({path:'/aboutUs',query:{menuFlag : this.menuFlag}})
            },
            find:function () {

	            switch (this.menuFlag){
		            //
		            case 'home':
			            this.$router.replace({path:'/search',query:{searchFlag:'home'}})
			            break;
		            //
		            case 'unlogin':
			            this.$router.replace({path:'/search',query:{searchFlag:'unlogin'}})
			            break;
		            //
		            case 's_home':
			            this.$router.replace({path:'/search',query:{searchFlag:'s_home'}})
			            break;
	            }

            }

		},
		computed: {
			...mapState({
				language: state => state.language.language
			})
		},

	};



