
	import { mapGetters } from 'vuex'
    import foot from '../foot.vue'
	import menu from '../common/menu.vue'
    import guestCenter from '../common/menu/guestCenter.vue'
    import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

    import { mapState } from 'vuex'
	export default {
		data() {
			return {
				dataList:[],
				tagList:[],
                nextPage:'1',
				noData:false,
				menuFlag:1,
				isItemShow:false
			};
		},
        created:function () {
			//
            localStorage.footFlag = 2;
            //
            let _this = this;
			$(document).ready(function () {
				var swiper = new Swiper('.promotiom .swiper-container', {
					pagination: '.swiper-pagination',
					slidesPerView: 2.5,
					paginationClickable: true,
					spaceBetween: 0
				});
				//
				$('.promotiom_index .swiper-wrapper>div').click(function () {
                    $(this).addClass('active').siblings().removeClass('active');
				})
			})
            //




        },
		methods: {
			infinite(done){
				done(true);
				//
                if(this.nextPage != '-1'){
                    if(this.menuFlag == 1){
	                    let params = {
		                    hotelid: localStorage.HOTELID,
		                    lang: localStorage.LANGUAGE,
		                    tagid:'',
		                    page:this.nextPage,
		                    limit:5
	                    };
	                    this.$store.dispatch('getPromotionList', params).then((res) => {
		                    this.dataList = this.dataList.concat(res.data.data.list)
		                    this.nextPage = res.data.data.nextPage
                            this.getFlag(this.dataList)
		                    done(true);
	                    })
                    }else if(this.menuFlag == 2){
	                    let params = {
		                    hotelid: localStorage.HOTELID,
		                    lang: localStorage.LANGUAGE,
		                    tagid:'',
		                    page:this.nextPage,
		                    limit:5
	                    };
	                    this.$store.dispatch('getNewsList', params).then((res) => {
		                    this.dataList = this.dataList.concat(res.data.data.list)
		                    this.nextPage = res.data.data.nextPage
		                    this.getFlag(this.dataList)
		                    done();
	                    })
                    }else if(this.menuFlag == 3){
	                    let params = {
		                    groupid: 1,
		                    tagid:'',
		                    page:this.nextPage,
		                    limit:5
	                    };
	                    this.$store.dispatch('getGroupNewsList', params).then((res) => {
		                    this.dataList = this.dataList.concat(res.data.data.list)
		                    this.nextPage = res.data.data.nextPage
		                    this.getFlag(this.dataList)
		                    done();
	                    })
                    }else if(this.menuFlag == 4){
	                    let params = {
		                    groupid: 1,
		                    tagid:'',
		                    page:this.nextPage,
		                    limit:5
	                    };
	                    this.$store.dispatch('getGroupActivityList', params).then((res) => {
		                    this.dataList = this.dataList.concat(res.data.data.list)
		                    this.nextPage = res.data.data.nextPage
		                    this.getFlag(this.dataList)
		                    done();
	                    })
                    }

                }else{
	                done(false);
	                this.noData = true;
                }


            },
            changeMenuFlag(flag) {
				//
				this.dataList=[];
	            this.nextPage = '1';
                this.menuFlag = flag;
            },
			goDetail(id) {
				let data = {};
				for(var key in this.dataList){
					if (this.dataList[key].id == id){
						data = this.dataList[key];
						break;
					}
				}
				this.$router.replace({path:'/promotionDetail',query:{info:data}});
			},
            getFlag:function (dataList) {
	            this.tagList= [];
	            //
	            for(var item in dataList){
	            	if(this.dataList[item].tagName !=''){
			            this.tagList.push(this.dataList[item].tagName)
                    }
	            }
	            console.log('hahahhahahahh',this.tagList)
            },
			toTag:function () {
                
			}

		},
        components:{
			'v-foot':foot,
            'v-menu':menu,
            'v-guestCenter':guestCenter,
            Divider,
            Spinner,
            PopupPicker,
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },
	};



