import { mapGetters } from 'vuex'
import { mapState } from 'vuex'
export default {
	data() {
		return {
		};
	},
	created:function () {

	},
	methods: {
		//
		goBack:function () {
			let flag = localStorage.menuFlag;
			switch (flag){
				//
				case 'home':
					this.$router.push('/home')
					break;
				//
				case 'unlogin':
					this.$router.push('/unlogin')
					break;
				//
				case 'guide':
					this.$router.push('/guide')
					break;
				//
				case 's_home':
					this.$router.push('/s_home')
					break;
			}

		}

	},
	mounted() {

	},
	components:{

	},
};



