    import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
    export default {
        data() {
            return {
                info:{},
                srcUrl:'',
            };
        },
        created:function () {
            this.info = this.$route.query.info;
        },
        methods: {
            toApply: function () {
                this.$router.replace({path:'/apply',query:{info:this.info,flag:1}});
            },
			goBack: function () {
                this.$router.replace({path:'/propertyActivity'});
//				window.history.go(-1)
            }
        },
        mounted() {

        },
        components:{

        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        }
    };



