    import { mapState } from 'vuex'
	export default {
		data() {
			return {
				telList:[]
			};
		},
        created:function () {
		    if (localStorage.TOKEN){
		    	let _this = this;
                let params = {'token':localStorage.TOKEN,'hotelid':localStorage.HOTELID};
                this.$store.dispatch('getHotelTelList', params).then(function (res) {
	                _this.telList = res.data.data.list;
                });

            }else{
		    	this.telList =[
                    {
                    	title:'Services',
                        telList: [
	                        {title:'ASCOTT CHINA',tel:'8621-2312-5800'},
	                        {title:'ASSISTANT MANAGER',tel:'1001'},
	                        {title:'RECEPTION MANAGER',tel:'1002'},
                        ]
                    },
				    {
					    title:'Facilities',
					    telList: [
						    {title:'RESTAURANT',tel:'1004'}
					    ]
				    }
                ]
            }
        },
		methods: {
		    call:function (phone) {
//		        alert(phone)
                window.location.href = "tel:"+phone;
            }
		},
        computed: {
            ...mapState({
                hotelTelList: state => state.translate.hotelTelList
            })
        }

	};



